
/*HEADER*/
@font-face{
  font-family:'helveticaNeue-bold';
  src:url(./fonts/helveticaNeue-bold.otf) format('opentype');
}

/*SUBHEADER*/
@font-face{
  font-family:'GothamRounded-Book';
  src:url(./fonts/GothamRounded-Book.otf) format('opentype');
}


/*BODY TEXT*/
@font-face{
  font-family:'HelveticaNeue-Medium';
  src:url(./fonts/HelveticaNeue-Medium.ttf) format('truetype');
}

*{
  box-sizing: border-box;
  margin:0;
  padding:0;
  /*--GreenStrong:#146c73;*/
  --GreenStrong: #146c73; ;
  --GreenLight:#00AAA9;
  --PinkLight:#eab485;
  --PinkStrong: #dbada4;
  --PinkDark: #a57964 ;
  --White:#fff;
 /*/ --Pink:#ecdccc;*/
  --HeaderBodyPrimary:var(--GreenStrong);
  --HeaderBodySecondary:var(--White);
  --HeaderBodyThird:var(--PinkStrong);
  --TextBodyPrimary:var(--PinkDark);
  --TextBodySecondary:var(--GreenStrong);
  --TextBodyThird:var(--PinkLight);
  --Header-Family:'helveticaNeue-bold',sans-serif;
  --Text-Family-Primary:'HelveticaNeue-Medium',sans-serif;
  --Text-Family-Secondary:'GothamRounded-Book',sans-serif;

  font-family:var(--Text-Family-Secondary);
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 1s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.1);
  }
}
